
html,
body {
  font-size: 10px;
  font-family: "Quicksand", sans-serif;
}
body {
  padding: 0;
  margin: 0;
}
:root {
  --main: #0c0c0c;
  --about: #420f21;
  --showcase: #23263d;
  --light: #fffcff;
  --text-color: #fffcff;
  --block-shadow: 0 1px 3px rgba(0, 0, 0, 0.4);
  --b-r: 5px;
  --accent: #eaba6b;
  --dark: #0c0c0c;
  --warning: #cf5c36;
}
body * {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
h2,
h3,
h4 {
  margin: 10px 0;
}
h2 {
  font-size: 4.2rem;
}
h3 {
  font-size: 2.8rem;
}
h4 {
  margin: 0;
  font-size: 1.8rem;
}
#app {
  height: 100vh;
  width: 100vw;
  max-height: 100%;
  overflow-y: auto;
  overflow-y: overlay;
  -webkit-transition: background-color 0.3s 0.2s;
  transition: background-color 0.3s 0.2s;
  background-color: var(--main);
  position: relative;
}
#app::-webkit-scrollbar {
    display: none;
}
.main {
  position: relative;
  top: 15%;
  max-width: 900px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  -webkit-transition: -webkit-filter 1s;
  transition: -webkit-filter 1s;
  transition: filter 1s;
  transition: filter 1s, -webkit-filter 1s;
  border-radius: var(--b-r);
}
.main > section {
    position: relative;
}
.main * {
    -webkit-perspective: 300px;
            perspective: 300px;
}
@-webkit-keyframes show-main {
0% {
    border-radius: 0px;
    background-color: var(--main);
    -webkit-transform: scale(0.99);
            transform: scale(0.99);
    opacity: 0;
}
20% {
    opacity: 1;
}
60% {
    -webkit-transform: scale(1.01);
            transform: scale(1.01);
    border-radius: 16px;
}
80% {
    background-color: var(--main);
}
100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    border-radius: 4px;
    background-color: var(--main-1);
    opacity: 1;
}
}
@keyframes show-main {
0% {
    border-radius: 0px;
    background-color: var(--main);
    -webkit-transform: scale(0.99);
            transform: scale(0.99);
    opacity: 0;
}
20% {
    opacity: 1;
}
60% {
    -webkit-transform: scale(1.01);
            transform: scale(1.01);
    border-radius: 16px;
}
80% {
    background-color: var(--main);
}
100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    border-radius: 4px;
    background-color: var(--main-1);
    opacity: 1;
}
}
@-webkit-keyframes hello-after {
from {
    opacity: 0;
}
to {
    opacity: 1;
}
}
@keyframes hello-after {
from {
    opacity: 0;
}
to {
    opacity: 1;
}
}
@-webkit-keyframes hello {
from {
    background-color: var(--dark);
}
to {
    background-color: var(--dark-1);
}
}
@keyframes hello {
from {
    background-color: var(--dark);
}
to {
    background-color: var(--dark-1);
}
}
@media screen and (max-width: 600px) {
html {
    font-size: 8px;
}
.main {
    top: 12%;
    padding-bottom: 50px;
}
}

.nav {
  background-color: var(--nav-bg, transparent);
  width: 100%;
  height: 50px;
  position: fixed;
  left: 0;
  z-index: 100;
  border-top-left-radius: 4px;
  overflow: hidden;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.nav-appear .nav-link {
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
    opacity: 0;
    -webkit-animation: showNavLinks 1.2s ease-out;
            animation: showNavLinks 1.2s ease-out;
}
.nav ul {
    list-style: none;
    padding: 0;
    margin: 0;
}
.nav .nav-link {
    display: inline-block;
    height: 50px;
    margin: 0 5px;
    padding: 5px;
    -webkit-transition: background-color 0.3s, opacity 0.3s, -webkit-backdrop-filter 0.5s;
    transition: background-color 0.3s, opacity 0.3s, -webkit-backdrop-filter 0.5s;
    transition: backdrop-filter 0.5s, background-color 0.3s, opacity 0.3s;
    transition: backdrop-filter 0.5s, background-color 0.3s, opacity 0.3s, -webkit-backdrop-filter 0.5s;
    opacity: 0.8;
    -webkit-backdrop-filter: blur(4px);
            backdrop-filter: blur(4px);
}
.nav .nav-link:hover {
      opacity: 1;
      background-color: var(--nav-link-hover-bg, transparent);
}
.nav .nav-link a {
      display: block;
      width: 100%;
      height: 100%;
      -webkit-transition: color 0.3s;
      transition: color 0.3s;
      color: var(--text-color);
      text-align: center;
      text-decoration: none;
      font-size: 18px;
      line-height: 38px;
      padding: 0 10px;
}
.nav .nav-link.router-link-exact-active {
      opacity: 1;
}
.nav .nav-link.router-link-exact-active a {
        color: var(--accent);
}
.nav .pointer {
    display: block;
    height: 2px;
    width: 10px;
    position: absolute;
    bottom: 0;
    left: 0;
    -webkit-transition: -webkit-transform 0.6s ease;
    transition: -webkit-transform 0.6s ease;
    transition: transform 0.6s ease;
    transition: transform 0.6s ease, -webkit-transform 0.6s ease;
    background-color: var(--accent);
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
    -webkit-transform-origin: left;
            transform-origin: left;
}
@-webkit-keyframes showNavLinks {
0% {
    -webkit-transform: translateY(-40px);
            transform: translateY(-40px);
    opacity: 0;
}
40% {
    opacity: 1;
}
70% {
    -webkit-transform: translateY(5px);
            transform: translateY(5px);
    opacity: 1;
}
100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 1;
}
}
@keyframes showNavLinks {
0% {
    -webkit-transform: translateY(-40px);
            transform: translateY(-40px);
    opacity: 0;
}
40% {
    opacity: 1;
}
70% {
    -webkit-transform: translateY(5px);
            transform: translateY(5px);
    opacity: 1;
}
100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 1;
}
}
@media screen and (max-width: 600px) {
.nav {
    --bg: var(--nav-bg, var(--main));
    top: auto;
    bottom: 0;
    width: 100%;
    background-color: var(--bg);
}
.nav .pointer {
      bottom: auto;
      top: 0;
}
}

.page-main {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-flow: column nowrap;
          flex-flow: column nowrap;
  min-height: 500px;
  background-color: transparent;
}
.page-main > * {
    display: block;
    width: 100%;
    position: relative;
}
.main-enter-active {
  -webkit-transition: opacity 0.8s;
  transition: opacity 0.8s;
}
.main-enter-active .heading,
  .main-enter-active .social,
  .main-enter-active .desc {
    -webkit-transition: opacity 0.6s, -webkit-transform 1s;
    transition: opacity 0.6s, -webkit-transform 1s;
    transition: transform 1s, opacity 0.6s;
    transition: transform 1s, opacity 0.6s, -webkit-transform 1s;
}
.main-enter .heading {
  -webkit-transform: translateY(-30px);
          transform: translateY(-30px);
}
.main-enter .desc {
  opacity: 0;
}
.main-enter .social {
  -webkit-transform: translateY(40px);
          transform: translateY(40px);
}
.about-leave-active,
.showcase-leave-active,
.main-leave-active {
  -webkit-transition: opacity 0.5s, -webkit-transform 0.5s;
  transition: opacity 0.5s, -webkit-transform 0.5s;
  transition: transform 0.5s, opacity 0.5s;
  transition: transform 0.5s, opacity 0.5s, -webkit-transform 0.5s;
}
.about-leave-active .heading,
  .about-leave-active .social,
  .about-leave-active .card-empty,
  .about-leave-active .card-promise,
  .showcase-leave-active .heading,
  .showcase-leave-active .social,
  .showcase-leave-active .card-empty,
  .showcase-leave-active .card-promise,
  .main-leave-active .heading,
  .main-leave-active .social,
  .main-leave-active .card-empty,
  .main-leave-active .card-promise {
    -webkit-transition: -webkit-transform 0.4s;
    transition: -webkit-transform 0.4s;
    transition: transform 0.4s;
    transition: transform 0.4s, -webkit-transform 0.4s;
}
.about-leave-active.about,
  .showcase-leave-active.about,
  .main-leave-active.about {
    -webkit-transition: opacity 0.8s;
    transition: opacity 0.8s;
}
.about-leave-active .about-main,
  .about-leave-active .about-main ~ *,
  .showcase-leave-active .about-main,
  .showcase-leave-active .about-main ~ *,
  .main-leave-active .about-main,
  .main-leave-active .about-main ~ * {
    -webkit-transition: opacity 0.6s, -webkit-transform 1s;
    transition: opacity 0.6s, -webkit-transform 1s;
    transition: opacity 0.6s, transform 1s;
    transition: opacity 0.6s, transform 1s, -webkit-transform 1s;
}
.showcase-leave-to,
.about-leave-to,
.main-leave-to {
  opacity: 0;
}
.showcase-leave-to .heading,
  .about-leave-to .heading,
  .main-leave-to .heading {
    -webkit-transform: translateY(-30px);
            transform: translateY(-30px);
}
.showcase-leave-to .social,
  .about-leave-to .social,
  .main-leave-to .social {
    -webkit-transform: translateY(40px);
            transform: translateY(40px);
}
.showcase-leave-to .card-empty,
  .about-leave-to .card-empty,
  .main-leave-to .card-empty {
    -webkit-transform: translateY(-100px);
            transform: translateY(-100px);
}
.showcase-leave-to .card-promise,
  .about-leave-to .card-promise,
  .main-leave-to .card-promise {
    -webkit-transform: translateY(200px);
            transform: translateY(200px);
}
.showcase-leave-to .about-main,
  .about-leave-to .about-main,
  .main-leave-to .about-main {
    opacity: 0;
    -webkit-transform: translate3d(0, -350px, -100px);
            transform: translate3d(0, -350px, -100px);
}
.showcase-leave-to .about-main ~ *,
    .about-leave-to .about-main ~ *,
    .main-leave-to .about-main ~ * {
      opacity: 0;
      -webkit-transform: translateY(150px);
              transform: translateY(150px);
}
.heading {
  font-size: 7rem;
  margin: 10% 4rem 0 0;
  text-align: right;
  -webkit-transition: color 0.4s;
  transition: color 0.4s;
  color: var(--accent);
  font-family: "Dosis", sans-serif;
}
.heading > span {
    opacity: 0;
    -webkit-animation: fadeIn 3s forwards;
            animation: fadeIn 3s forwards;
}
.desc {
  font-size: 3rem;
  text-align: right;
  padding: 0;
  margin: 0 4rem 0 0;
  margin-top: 1rem;
  color: var(--text-color);
}
.social {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-flow: row nowrap;
          flex-flow: row nowrap;
  position: absolute;
  bottom: 0;
}
.link {
  position: relative;
  padding: 10px;
  margin: 10px;
  display: inline-block;
  width: 50px;
  height: 50px;
}
.link:hover svg {
  fill: var(--accent);
}
.link svg {
  width: 100%;
  height: 100%;
  fill: var(--text-color);
  -webkit-transition: fill 0.4s;
  transition: fill 0.4s;
}
@-webkit-keyframes fadeIn {
from {
    opacity: 0;
}
to {
    opacity: 1;
}
}
@keyframes fadeIn {
from {
    opacity: 0;
}
to {
    opacity: 1;
}
}
@media screen and (max-width: 760px) {
.heading,
  .desc {
    margin-right: 5px;
}
.heading {
    font-size: 5rem;
}
.desc {
    font-size: 2.4rem;
}
}
@media screen and (max-width: 600px) {
.page-main {
    min-height: 400px;
}
.container {
    padding-right: 10px;
}
}

.about {
  color: var(--text-color);
  min-height: 600px;
  padding: 0 20px;
}
.about-enter {
    opacity: 0;
    background-color: transparent;
}
.about-enter .about-img img {
      -webkit-clip-path: circle(0% at 10% 10%);
              clip-path: circle(0% at 10% 10%);
}
.about-enter .about-info {
      -webkit-transform: translateX(120px);
              transform: translateX(120px);
}
.about-enter .about-who {
      -webkit-transform: translateY(300px);
              transform: translateY(300px);
}
.about-enter-active {
    -webkit-transition: background-color 0.7s, opacity 0.3s, -webkit-clip-path 0.9s;
    transition: background-color 0.7s, opacity 0.3s, -webkit-clip-path 0.9s;
    transition: background-color 0.7s, opacity 0.3s, clip-path 0.9s;
    transition: background-color 0.7s, opacity 0.3s, clip-path 0.9s, -webkit-clip-path 0.9s;
}
.about-enter-active .about-img img {
      -webkit-transition: -webkit-clip-path 0.9s;
      transition: -webkit-clip-path 0.9s;
      transition: clip-path 0.9s;
      transition: clip-path 0.9s, -webkit-clip-path 0.9s;
}
.about-enter-active .about-info {
      -webkit-transition: -webkit-transform 0.9s;
      transition: -webkit-transform 0.9s;
      transition: transform 0.9s;
      transition: transform 0.9s, -webkit-transform 0.9s;
}
.about-enter-active .about-who {
      -webkit-transition: -webkit-transform 0.9s;
      transition: -webkit-transform 0.9s;
      transition: transform 0.9s;
      transition: transform 0.9s, -webkit-transform 0.9s;
}
@media screen and (max-width: 600px) {
.about {
    padding: 0 1rem;
    -webkit-perspective: 2000px;
            perspective: 2000px;
}
}

.showcase-enter {
  -webkit-transform: translateY(100px);
          transform: translateY(100px);
  opacity: 0;
}
.showcase-enter .card-promise {
    -webkit-transform: translateY(300px);
            transform: translateY(300px);
}
.showcase-enter-active {
  -webkit-transition: opacity 0.4s, -webkit-transform 1s ease;
  transition: opacity 0.4s, -webkit-transform 1s ease;
  transition: transform 1s ease, opacity 0.4s;
  transition: transform 1s ease, opacity 0.4s, -webkit-transform 1s ease;
}
.showcase-enter-active .card-promise {
    -webkit-transition: -webkit-transform 1s;
    transition: -webkit-transform 1s;
    transition: transform 1s;
    transition: transform 1s, -webkit-transform 1s;
}
